import Vue from 'vue'
import Router from 'vue-router'
import Carousel from '../components/Carousel.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/carousel:id',
      name: 'carousel',
      component: Carousel,
      props: true
    }
  ]
})



