<template>
  <div id="app">
    <el-row :gutter="40" style="padding: 40px; margin: 0;">
      <el-col :xs="24" :sm="12" :md="6" :lg="6" v-for="product in products" :key="product.id" style="margin-bottom: 40px;">
        <div class="product" @click="openSides(product.id)">
          <img class="product__image" :src="product.sides[0].small" alt="image">
          <div class="product__info">
            <p class="product__category">{{ product.category.title }}</p>
            <p class="product__title">{{ product.title }}</p>
            <p>
              <span v-show="product.old_price" class="product__oldprice">{{ product.old_price }} .</span>
              <span class="product__price" :class="{ 'product__price-sale': product.old_price }">{{ product.price }} .</span>
            </p>
          </div>
        </div>
      </el-col>
    </el-row>
    <transition name="fade" mode="out-in">
      <router-view></router-view>
    </transition>
  </div>
</template>

<script>
  import axios from 'axios'
  import router from './router'

  export default {
    name: 'app',
    data () {
      return {
        products: []
      }
    },
    methods: {
      fetchProducts() {
        let self = this;
        axios.get('https://rest.vsemayki.ru/catalog/items?access-token=f6ed359e9eab534b09d846ed56e6e196e7295076')
          .then(function (response) {
            self.products = response.data.items;
          })
          .catch(function (error) {
            console.log(error);
          })
      },
      productById: function (id) {
        let self = this;
        for (let item in self.products) {
          if (self.products[item].id === id) {
            return self.products[item]
          }
        }
      },
      openSides(productId) {
        router.push({ name: 'carousel', params: { id: productId, product: this.productById(productId) } })
        setTimeout(function() {
          window.scrollTo(0, document.body.scrollHeight);
        }, 100);
      }
    },
    mounted() {
      this.fetchProducts()
    }
  }
</script>

<style>
body {
  font-family: Arial, Helvetica, sans-serif;
  color: #333;
}

[v-cloak] {
  opacity: 0;
}

.fade-enter-active, .fade-leave-active {
  -webkit-transition: opacity 0.2s;
  transition: opacity 0.2s;
}

.fade-enter, .fade-leave-to {
  opacity: 0;
}

.clearfix:before, .clearfix:after {
  display: table;
  content: "";
}

.clearfix:after {
  clear: both;
}

.product {
  cursor: pointer;
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
  outline: 1px solid transparent;
}

.product:hover {
  outline: 1px solid #00AEEF;
}

.product__image {
  display: block;
  width: 100%;
}

.product__info {
  padding-left: 7px;
  padding-bottom: 4px;
}

.product__category {
  color: #666;
  margin-bottom: 6px;
  font-size: 12px;
  font-weight: 400;
}

.product__title {
  font-size: 16px;
  max-height: 16px;
  margin-top: 0;
  margin-bottom: 2px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.product__price {
  font-size: 18px;
}

.product__oldprice {
  text-decoration: line-through;
  font-size: 14px;
}

.product__price-sale {
  color: #E2071C;
}
</style>



