<template>
  <div class="carousel">
    <div v-if="product">
      <h1 class="carousel__title">{{ product.title }}</h1>
      <el-carousel type="card" height="500px" :interval="4000"> 
        <el-carousel-item v-for="item in product.sides" :key="item.position">
          <div class="carousel__image">
            <img :src="item.big" alt="">
          </div>
        </el-carousel-item>
      </el-carousel>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    id: {
      type: String,
      default: ''
    },
    product: {
      type: Object,
      default: () => {}
    }
  }
}
//
</script>


<style>
  .carousel {
    margin: 40px;
  }

  .carousel__title {
    text-align: center;
  }

  .carousel__image {
    height: 100%;
    border-top: 3px solid #00AEEF;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
  }

  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 200px;
    margin: 0;
  }

  .el-carousel__item:nth-child(2n) {
    background-color: #99A9BF;
  }

  .el-carousel__item:nth-child(2n+1) {
    background-color: #D3DCE6;
  }
</style>


